import React from 'react';
import styled from 'styled-components';

import shadowImage from './shadow.svg';

const badgeHeightPx = 86;

const StyledBody = styled.div`
    position: relative;

    height: ${badgeHeightPx}px;
    min-width: ${badgeHeightPx}px;
    padding: 6px;
    border-radius: ${badgeHeightPx / 2}px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    text-align: center;
`;

const kernelWidthPx = 4;
const kernelBorderRadiusPx = kernelWidthPx / 2;

const StyledKernel = styled.div`
    height: 34px;
    width: ${kernelWidthPx}px;

    border-bottom-left-radius: ${kernelBorderRadiusPx}px;
    border-bottom-right-radius: ${kernelBorderRadiusPx}px;
`;

const StyledShadow = styled.div`
    margin-top: -8px;

    width: 86px;
    height: 18px;

    background-image: url('${shadowImage}');
`;

interface PinInterface {
    pinColor: string;
}

const StyledPin = styled.div<PinInterface>`
    display: flex;
    flex-direction: column;
    align-items: center;

    ${StyledBody}, ${StyledKernel} {
        background-color: ${({ pinColor }) => pinColor};
    }
`;

export const Pin: React.FC<PinInterface> = ({ pinColor, children }) => {
    return (
        <StyledPin pinColor={pinColor}>
            <StyledBody>{children}</StyledBody>
            <StyledKernel />
            <StyledShadow />
        </StyledPin>
    );
};
