import React from 'react';
import styled, { css } from 'styled-components';
import { useFocusOnMount } from 'hooks/useFocusOnMount';
import { color } from 'style/const/color';
import { focusBorderMixin } from 'style/mixin/focus';
import { font } from 'style/const/font';

export enum ButtonViews {
    primary = 'primary',
    secondary = 'secondary',
}

export enum ButtonSize {
    l = 'l',
    m = 'm',
    s = 's',
}

interface ButtonParams {
    borderRadiusPx: number;
    paddingXPx: number;
    paddingYPx: number;
    fontSizePx: number;
    lineHeightPx: number;
}

const mapSizeToButtonParams: Record<ButtonSize, ButtonParams> = {
    [ButtonSize.l]: {
        borderRadiusPx: 32,
        paddingXPx: 56,
        paddingYPx: 32,
        fontSizePx: 32,
        lineHeightPx: 40,
    },
    [ButtonSize.m]: {
        borderRadiusPx: 24,
        paddingXPx: 48,
        paddingYPx: 24,
        fontSizePx: 32,
        lineHeightPx: 40,
    },
    [ButtonSize.s]: {
        borderRadiusPx: 24,
        paddingXPx: 28,
        paddingYPx: 16,
        fontSizePx: 28,
        lineHeightPx: 32,
    },
};

const buttonViewBackgroundMap: Record<ButtonViews, string> = {
    primary: color.button.primary,
    secondary: color.button.secondary1,
};

interface StyledButtonProps {
    view: ButtonViews;
    size: ButtonSize;
}
const StyledButton = styled.div<StyledButtonProps>`
    position: relative;

    ${({ size }) => {
        const { borderRadiusPx, paddingXPx, paddingYPx, fontSizePx, lineHeightPx } = mapSizeToButtonParams[size];

        return css`
            padding: ${paddingYPx}px ${paddingXPx}px;
            font-size: ${fontSizePx}px;
            line-height: ${lineHeightPx}px;
            border-radius: ${borderRadiusPx}px;
        `;
    }}

    border: none;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: background-color 0.3s ease-in-out;

    background-color: ${({ view }) => buttonViewBackgroundMap[view]};

    font-family: inherit;
    font-weight: ${font.weight.semibold};
    color: ${color.text.primary};

    ${({ size }) => focusBorderMixin({ borderRadius: mapSizeToButtonParams[size].borderRadiusPx })}

    &[disabled] {
        opacity: 0.5;
    }
`;

export interface ButtonProps {
    className?: string;
    shouldFocusOnMount?: boolean;
    forwardRef?: React.RefObject<HTMLButtonElement>;
    view: ButtonViews;
    disabled?: boolean;
    size?: ButtonSize;
    shouldLockAfterClick?: boolean;
    lockTimeout?: number;
    onClick?: (event: React.SyntheticEvent<HTMLButtonElement>) => void;
    onFocus?: (event: React.FocusEvent<HTMLButtonElement>) => void;
}

const pressedStateTimeoutMs = 8000;

const usePressedState = ({
    disabled,
    shouldLockAfterClick,
    lockTimeout = pressedStateTimeoutMs,
    onClick,
}: Pick<ButtonProps, 'disabled' | 'shouldLockAfterClick' | 'lockTimeout' | 'onClick'>) => {
    const [timeoutId, setTimeoutId] = React.useState(0);

    // если таймаут установлен - кнопка заблокирована
    const isBlocked = Boolean(timeoutId);

    const handleClick = React.useCallback(
        (event: React.SyntheticEvent<HTMLButtonElement>) => {
            if (shouldLockAfterClick && !timeoutId) {
                setTimeoutId(setTimeout(() => setTimeoutId(0), lockTimeout));
            }
            onClick?.(event);
        },
        [shouldLockAfterClick, lockTimeout, timeoutId, onClick],
    );

    React.useEffect(() => {
        const id = timeoutId;
        return () => {
            if (id) {
                clearTimeout(id);
            }
        };
    }, [timeoutId]);

    return {
        isDisabled: disabled || isBlocked,
        handleClick,
    };
};

export const Button: React.FC<ButtonProps> = ({
    shouldFocusOnMount,
    forwardRef,
    shouldLockAfterClick,
    lockTimeout,
    onClick,
    disabled,
    size = ButtonSize.m,
    ...props
}) => {
    const mountFocusRef = useFocusOnMount<HTMLButtonElement>();
    const ref = forwardRef || (shouldFocusOnMount ? mountFocusRef : null);
    const { isDisabled, handleClick } = usePressedState({
        disabled,
        shouldLockAfterClick,
        lockTimeout,
        onClick,
    });

    return (
        <StyledButton
            as="button"
            type="button"
            size={size}
            ref={ref}
            disabled={isDisabled}
            onClick={handleClick}
            {...props}
        />
    );
};
